import {Component, OnInit} from '@angular/core';
import {Pageable} from './pageable';
import { NavigationService } from '../core/navigation.service';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.scss']
})
export class WelcomeComponent implements OnInit {

  pageable: Pageable;
  anchors;
  currentPageIndex = 0;
  displayMobileMenu = false;
  constructor(private navigationSrv: NavigationService) {}

  ngOnInit() {
    this.pageable = new window['Pageable']('#pageable-container', {
      animation: 400,
      onFinish: this.update,
      events: {mouse: false}
    });
    this.pageable.initialised = false;
    this.pageable.update();
    // this.pageable.scrollToIndex(0)
  }



  gotoSection(number: number) {
   if (this.pageable) {
     this.pageable.scrollToIndex(number);
   }
  }

  private update(data) {
    this.currentPageIndex = data.index;
  }

  toggleMobileMenu() {
    this.displayMobileMenu = !this.displayMobileMenu;
  }

  onNavigateHome() {
    this.gotoSection(0);
  }

  navigateByHash(hashLink: string) {
    this.navigationSrv.navigateByHash(hashLink);
    this.displayMobileMenu = false;
  }

  navigateByRouterLink(path: string) {
    this.navigationSrv.navigateByRouterLink(path);
    this.displayMobileMenu = false;
  }
}
export interface PageableEvents {
  wheel: boolean;
  mouse: boolean;
  touch: boolean;
  keydown: boolean;
}
export interface PageableOptions {
  pips: boolean;
  animation: number;
  delay: number;
  throttle: number;
  orientation: string;
  easing: Function;
  onInit: Function;
  onUpdate: Function;
  onBeforeStart: Function;
  onStart: Function;
  onScroll: Function;
  swipeThreshold: number;
  freeScroll: boolean;
  slideshow: boolean;
  infinite: boolean;
  childSelector: string;
  events: PageableEvents;
}
export interface PageableAxis {
  x: string;
  y: string;
}

export interface Callbacks {
  wheel: any;
  update: any;
  load: any;
  start: any;
  drag: any;
  stop: any;
  click: any;
  prev: any;
  next: any;
  keydown: any;
}

export interface Size {
  width: number;
  height: number;
}

export interface PageableData {
  window: Size;
  container: Size;
}

